<template>
  <div>
    <Loading v-if="loading == true"/>
    <div v-else>
      <!--通知栏-->
      <notice />
      <!--店铺基本信息-->
      <div class="index_storeMessage">
        <img src="https://ss3.baidu.com/-rVXeDTa2gU2pMbgoY3K/it/u=229445282,848378121&fm=202&mola=new&crop=v1" />
        <span style="padding:0 26px;">摇滚米粒(关谷一店)</span>
        <el-rate
          v-model="storeComment"
          disabled
          show-score
          text-color="#ff9900"
          score-template="{value}">
        </el-rate>
      </div>
      <!--店铺信息栏-->
      <div class="index_storeNub">
        <ul style="display:flex;">
          <li>
            <span>今日营业额</span>
            <span style="font-size:20px;color:#000;">￥ 1477.28 元</span>
            <div>昨日 1219.10元
              <span style="margin-left:15px;color:#8BC34A!important"><i style="font-size:23px;padding-right:10px;" class="el-icon-sort-up"></i>
              214.10元</span>
            </div>
          </li>
          <li>
            <span>今日订单量</span>
            <span style="font-size:20px;color:#000;">122 单</span>
            <div>昨日 131单
              <span style="margin-left:15px;color:#8BC34A!important"><i style="font-size:23px;padding-right:10px;" class="el-icon-sort-up"></i>
              9单</span>
            </div>
          </li>
          <li>
            <span>今日访客数</span>
            <span style="font-size:20px;color:#000;">1 个</span>
            <div>昨日访客数 1000个
              <span style="margin-left:15px;color:#E51C23!important"><i style="font-size:23px" class="el-icon-sort-down"></i>
              9个</span>
            </div>
          </li>
          <li>
            <span>代办事项</span>
            <div class="index_switch">
              <div>
                <span style="font-size:20px;color:#000;">外卖</span>
                <el-switch
                  class="index_switch_small"
                  v-model="goHome"
                  active-color="#13ce66"
                  inactive-color="#cccccc">
                </el-switch>
              </div>
              <div>
                <span style="font-size:20px;color:#000;">自提</span>
                <el-switch
                  class="index_switch_small"
                  v-model="goStore"
                  active-color="#13ce66"
                  inactive-color="#cccccc">
                </el-switch>
              </div>
            </div>
            <div class="index_moedth">
              <span>待接单 1单</span>
              <span>待自提 1单</span>
            </div>
          </li>
        </ul>
      </div>
      <!--店铺总营业额分布-->
      <div class="index_totalPrice">
        <div class="index_totalPrice_left">
          <span>总营业额分布</span>
          <el-radio-group v-model="totalPriceChoose" class="totalPriceChoose">
            <el-radio-button label="0">昨日</el-radio-button>
            <el-radio-button label="1">7日</el-radio-button>
            <el-radio-button label="2">30日</el-radio-button>
          </el-radio-group>
          <div id="myPiePrice" class="myPie"></div>
          <div class="index_totalPrice_bottom">
            <span style="color:#8BC34A">自提</span>
            <span style="color:#FF9800">外卖</span>
          </div>
        </div>
        <div class="index_totalPrice_right">
          <span>自有平台营业额</span>
          <div class="index_time">
            <el-radio-group v-model="totalPriceChooseLine" class="totalPriceChoose" style="margin-right:26px;">
              <el-radio-button label="0">7日</el-radio-button>
              <el-radio-button label="1">30日</el-radio-button>
              <el-radio-button label="2">90日</el-radio-button>
            </el-radio-group>
            <el-date-picker
              size="small"
              v-model="dateTimePrice"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>
          <div id="myLinePrice" class="myLine"></div>
          <div class="index_totalPrice_line_bottom">
            <span style="color:#FF4081" class="index_totalPrice_line_bottom_inline">全部<i style="background-color:#FF4081"></i></span>
            <span style="color:#8BC34A" class="index_totalPrice_line_bottom_inline">自提<i style="background-color:#8BC34A"></i></span>
            <span style="color:#FF9800" class="index_totalPrice_line_bottom_inline">外卖<i style="background-color:#FF9800"></i></span>
          </div>
        </div>
      </div>
      <!--店铺总订单量分布-->
      <div class="index_totalPrice">
        <div class="index_totalPrice_left">
          <span>总订单量分布</span>
          <el-radio-group v-model="totalNubChoose" class="totalPriceChoose">
            <el-radio-button label="0">昨日</el-radio-button>
            <el-radio-button label="1">7日</el-radio-button>
            <el-radio-button label="2">30日</el-radio-button>
          </el-radio-group>
          <div id="myPieNub" class="myPie"></div>
          <div class="index_totalPrice_bottom">
            <span style="color:#8BC34A">自提</span>
            <span style="color:#FF9800">外卖</span>
          </div>
        </div>
        <div class="index_totalPrice_right">
          <span>自有平台订单量</span>
          <div class="index_time">
            <el-radio-group v-model="totalNubChooseLine" class="totalPriceChoose" style="margin-right:26px;">
              <el-radio-button label="0">7日</el-radio-button>
              <el-radio-button label="1">30日</el-radio-button>
              <el-radio-button label="2">90日</el-radio-button>
            </el-radio-group>
            <el-date-picker
              size="small"
              v-model="dateTimeNub"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>
          <div id="myLineNub" class="myLine"></div>
          <div class="index_totalPrice_line_bottom">
            <span style="color:#FF4081" class="index_totalPrice_line_bottom_inline">全部<i style="background-color:#FF4081"></i></span>
            <span style="color:#8BC34A" class="index_totalPrice_line_bottom_inline">自提<i style="background-color:#8BC34A"></i></span>
            <span style="color:#FF9800" class="index_totalPrice_line_bottom_inline">外卖<i style="background-color:#FF9800"></i></span>
          </div>
        </div>
      </div>
      <!--店铺复购率及用户反馈-->
      <div class="index_totalPrice">
        <div class="index_totalPrice_left">
          <span>复购率: 60%</span>
          <el-radio-group v-model="totalPeopleChoose" class="totalPriceChoose">
            <el-radio-button label="0">昨日</el-radio-button>
            <el-radio-button label="1">7日</el-radio-button>
            <el-radio-button label="2">30日</el-radio-button>
          </el-radio-group>
          <div id="myPiePeople" class="myPie"></div>
          <div class="index_totalPrice_bottom">
            <span style="color:#8BC34A">新客</span>
            <span style="color:#FF9800">老客</span>
          </div>
        </div>
        <div class="index_totalPrice_right">
          <span>自有平台负面反馈</span>
          <div class="index_time">
            <el-radio-group v-model="totalPeopleChooseLine" class="totalPriceChoose" style="margin-right:26px;">
              <el-radio-button label="0">昨日</el-radio-button>
              <el-radio-button label="1">7日</el-radio-button>
              <el-radio-button label="2">30日</el-radio-button>
            </el-radio-group>
            <el-date-picker
              size="small"
              v-model="dateTimePeople"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>
          <div id="myLinePeople" class="myBar"></div>
        </div>
      </div>
      <!--各种情况的几率-->
      <div class="index_allBottom">
        <span>差评率: 6%</span>
        <span>拒单率: 5%</span>
        <span>少餐送错率: 2%</span>
        <span>配送延迟率: 1%</span>
        <span>超时取消率: 3%</span>
        <span>取消不告知率: 3%</span>
      </div>
    </div>
  </div>
</template>

<script>
import notice from './../components/notice'
// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入线状图组件
import 'echarts/lib/chart/line'
// 引入柱状图组件
import 'echarts/lib/chart/bar'
// 引入饼图。
import 'echarts/lib/chart/pie';
// 引入提示框和title组件
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'

import Loading from './../components/loading.vue'
export default {
    data () {
      return {
        loading:false,
        storeComment:4.7,  //店铺打分
        goHome:true,      //是否开启外卖
        goStore:false,    //是否开启自提
        totalPriceChoose:0,   //总营业额时间选择
        totalNubChoose:0,     //总订单量时间选择
        totalPeopleChoose:0,  //总复购率时间选择
        totalPriceChooseLine:0,   //线型图总营业额时间选择
        totalNubChooseLine:0,     //线型图总订单量时间选择
        totalPeopleChooseLine:0,  //线型图反馈情况时间选择
        dateTimePrice:'',   //总营业额时间段选择
        dateTimeNub:'',     //总订单量时间段选择
        dateTimePeople:'',   //总反馈情况时间段选择
        listTotal:[{
        "appType":1,
        "orderCount":120
        },
        {
        "appType":3,
        "orderCount":12
        }],  //实例数据集合 
        listSeven:[{
        "orderPriceSum":38.94,
        "orderCount":1,
        "orderDate":"08-15"
        },
        {
        "orderPriceSum":56.29,
        "orderCount":3,
        "orderDate":"08-16"
        },
        {
        "orderPriceSum":59.61,
        "orderCount":3,
        "orderDate":"08-17"
        },
        {
        "orderPriceSum":49.45,
        "orderCount":2,
        "orderDate":"08-18"
        },
        {
        "orderPriceSum":0.00,
        "orderCount":0,
        "orderDate":"08-19"
        },
        {
        "orderPriceSum":55.01,
        "orderCount":3,
        "orderDate":"08-20"
        },
        {
        "orderPriceSum":0.00,
        "orderCount":0,
        "orderDate":"08-21"
        }]   //实例数据集合
      }
    },
    created(){
      //这里需要注意可视化图标需要提前获取到dom 否则报错  ====> 体验提升解决方案 可以先在mounted里先将数据设为空 再在created里进行获取数据
    },
    mounted(){
      // this.loading = true
      //测试加载动画
      // setTimeout(()=>{
      //   this.loading = false
      // },2000)
      
      // 绘制饼图
      this.drawPiePrice(this.listTotal);  
      this.drawPieNub(this.listTotal); 
      this.drawPiePeople(this.listTotal); 
      // 绘制柱状图
      this.drawLinePrice(this.listSeven);
      this.drawLineNub(this.listSeven);
      this.drawLinePeople(this.listSeven);
    },
    methods:{
      //总营业额饼形
      drawPiePrice(listTotal) {
        let list = []
        for(let i =0;i<listTotal.length;i++){
          if(listTotal[i].appType == 1){
            list.push({
              value:listTotal[i].orderCount,
              name:'微信',
              selected:true,
              label:{
                normal:{
                  formatter: '{title|{c}元}',
                  position: 'inner',
                  rich:{
                    title:{
                      color:'#8BC34A',
                      align: 'center'
                    }
                  }
                }
              }
            })
          }else if(listTotal[i].appType == 3){
            list.push({
              value:listTotal[i].orderCount,
              name:'小程序',
              label:{
                normal:{
                  formatter: '{title|{c}元}',
                  position: 'inner',
                  rich:{
                    title:{
                      color:'#FF9800',
                      align: 'center'
                    }
                  }
                }
              }
            })
          }
        }
        echarts.init(document.getElementById('myPiePrice')).setOption({
          baseOption: {
              series : [
                  {
                      name: '访问来源',
                      type: 'pie',
                      radius : '55%',
                      center: ['50%', '50%'],
                      // roseType: 'radius',
                      // label: {
                      //     normal: {
                      //         show: true,
                      //         // formatter: '{b}: {c}({d}%)'
                      //         formatter: '{c}元',
                      //         position: 'inner'
                      //     }
                      // },
                      // color:['#8BC34A','#FF9800'],
                      backgroundColor:'#101010',
                      data:list,
                      itemStyle:{
                        normal: {
                            color: '#101010'
                        }
                      }
                  }
              ]
          },
          media: [

          ]
        })
      },
      //总营业额条形
      drawLinePrice(listSeven){
        let date = []
        for(let i = 0;i<listSeven.length;i++){
          date.push(listSeven[i].orderDate)
        }
        let moneyTotal = []
        for(let i = 0;i<listSeven.length;i++){
          moneyTotal.push(listSeven[i].orderPriceSum)
        }
        let orderTotal = []
        for(let i = 0;i<listSeven.length;i++){
          orderTotal.push(listSeven[i].orderCount)
        }
        this.$stamp(null,date)
        this.$stamp(null,moneyTotal)
        this.$stamp(null,orderTotal)

        echarts.init(document.getElementById('myLinePrice')).setOption({
          baseOption: {
              tooltip: {
                  trigger: 'axis'
              },
              legend: {
                  data:['总金额','总订单']
              },
              toolbox: {
                  show: true,
                  feature: {
                      magicType: {show: true, type: ['stack', 'tiled']},
                      saveAsImage: {show: true}
                  }
              },
              xAxis: {
                  name:'过去七天',
                  type: 'category',
                  boundaryGap: false,
                  data: date
              },
              yAxis: {
                  name:'营业额',
                  type: 'value'
              },
              series: [{
                  name: '全部',
                  type: 'line',
                  smooth: true,
                  color:'#FF4081',
                  data: moneyTotal
              },
              {
                  name: '自提',
                  type: 'line',
                  smooth: true,
                  color:'#8BC34A',
                  data: orderTotal
              },
              {
                  name: '外卖',
                  type: 'line',
                  smooth: true,
                  color:'#FF9800',
                  data: orderTotal
              }]
          },
          media: [ 
            {
                query: {maxWidth: 1150, minWidth: 1100},
                option: {
                    legend: {
                        orient: 'horizontal',
                        left: 200,
                        itemGap: 5
                    },
                    grid: {
                        left: '10%',
                        top: 80,
                        right: 250,
                        bottom: 100
                    },
                    xAxis: {
                        nameLocation: 'end',
                        nameGap: 10,
                        splitNumber: 5,
                        splitLine: {
                            show: true
                        }
                    },
                    timeline: {
                        orient: 'horizontal',
                        inverse: false,
                        left: '20%',
                        right: '20%',
                        bottom: 10,
                        height: 40
                    },
                    series: [
                        { center: ['50%', '50%']}
                    ]
                }
            }
          ]
        })
      },
      //总订单量饼形
      drawPieNub(listTotal) {
        let list = []
        for(let i =0;i<listTotal.length;i++){
          if(listTotal[i].appType == 1){
            list.push({
              value:listTotal[i].orderCount,
              name:'微信',
              selected:true,
              label:{
                normal:{
                  formatter: '{title|{c}元}',
                  position: 'inner',
                  rich:{
                    title:{
                      color:'#8BC34A',
                      align: 'center'
                    }
                  }
                }
              }
            })
          }else if(listTotal[i].appType == 3){
            list.push({
              value:listTotal[i].orderCount,
              name:'小程序',
              label:{
                normal:{
                  formatter: '{title|{c}元}',
                  position: 'inner',
                  rich:{
                    title:{
                      color:'#FF9800',
                      align: 'center'
                    }
                  }
                }
              }
            })
          }
        }
        echarts.init(document.getElementById('myPieNub')).setOption({
          baseOption: {
              series : [
                  {
                      name: '访问来源',
                      type: 'pie',
                      radius : '55%',
                      center: ['50%', '50%'],
                      // roseType: 'radius',
                      // label: {
                      //     normal: {
                      //         show: true,
                      //         // formatter: '{b}: {c}({d}%)'
                      //         formatter: '{c}元',
                      //         position: 'inner'
                      //     }
                      // },
                      // color:['#8BC34A','#FF9800'],
                      backgroundColor:'#101010',
                      data:list,
                      itemStyle:{
                        normal: {
                            color: '#101010'
                        }
                      }
                  }
              ]
          },
          media: [

          ]
        })
      },
      //总订单量条形
      drawLineNub(listSeven){
        let date = []
        for(let i = 0;i<listSeven.length;i++){
          date.push(listSeven[i].orderDate)
        }
        let moneyTotal = []
        for(let i = 0;i<listSeven.length;i++){
          moneyTotal.push(listSeven[i].orderPriceSum)
        }
        let orderTotal = []
        for(let i = 0;i<listSeven.length;i++){
          orderTotal.push(listSeven[i].orderCount)
        }
        this.$stamp(null,date)
        this.$stamp(null,moneyTotal)
        this.$stamp(null,orderTotal)

        echarts.init(document.getElementById('myLineNub')).setOption({
          baseOption: {
              tooltip: {
                  trigger: 'axis'
              },
              legend: {
                  data:['总金额','总订单']
              },
              toolbox: {
                  show: true,
                  feature: {
                      magicType: {show: true, type: ['stack', 'tiled']},
                      saveAsImage: {show: true}
                  }
              },
              xAxis: {
                  name:'过去七天',
                  type: 'category',
                  boundaryGap: false,
                  data: date
              },
              yAxis: {
                  name:'订单量',
                  type: 'value'
              },
              series: [{
                  name: '全部',
                  type: 'line',
                  smooth: true,
                  color:'#FF4081',
                  data: moneyTotal
              },
              {
                  name: '自提',
                  type: 'line',
                  smooth: true,
                  color:'#8BC34A',
                  data: orderTotal
              },
              {
                  name: '外卖',
                  type: 'line',
                  smooth: true,
                  color:'#FF9800',
                  data: orderTotal
              }]
          },
          media: [ 
            {
                query: {maxWidth: 1150, minWidth: 1100},
                option: {
                    legend: {
                        orient: 'horizontal',
                        left: 200,
                        itemGap: 5
                    },
                    grid: {
                        left: '10%',
                        top: 80,
                        right: 250,
                        bottom: 100
                    },
                    xAxis: {
                        nameLocation: 'end',
                        nameGap: 10,
                        splitNumber: 5,
                        splitLine: {
                            show: true
                        }
                    },
                    timeline: {
                        orient: 'horizontal',
                        inverse: false,
                        left: '20%',
                        right: '20%',
                        bottom: 10,
                        height: 40
                    },
                    series: [
                        { center: ['50%', '50%']}
                    ]
                }
            }
          ]
        })
      },
      //复购率饼形
      drawPiePeople(listTotal) {
        let list = []
        for(let i =0;i<listTotal.length;i++){
          if(listTotal[i].appType == 1){
            list.push({
              value:listTotal[i].orderCount,
              name:'新客',
              selected:true,
              label:{
                normal:{
                  formatter: '{title|{c}人}',
                  position: 'inner',
                  rich:{
                    title:{
                      color:'#8BC34A',
                      align: 'center'
                    }
                  }
                }
              }
            })
          }else if(listTotal[i].appType == 3){
            list.push({
              value:listTotal[i].orderCount,
              name:'老客',
              label:{
                normal:{
                  formatter: '{title|{c}人}',
                  position: 'inner',
                  rich:{
                    title:{
                      color:'#FF9800',
                      align: 'center'
                    }
                  }
                }
              }
            })
          }
        }
        echarts.init(document.getElementById('myPiePeople')).setOption({
          baseOption: {
              series : [
                  {
                      name: '访问来源',
                      type: 'pie',
                      radius : '55%',
                      center: ['50%', '50%'],
                      // roseType: 'radius',
                      // label: {
                      //     normal: {
                      //         show: true,
                      //         // formatter: '{b}: {c}({d}%)'
                      //         formatter: '{c}元',
                      //         position: 'inner'
                      //     }
                      // },
                      // color:['#8BC34A','#FF9800'],
                      backgroundColor:'#101010',
                      data:list,
                      itemStyle:{
                        normal: {
                            color: '#101010'
                        }
                      }
                  }
              ]
          },
          media: [

          ]
        })
      },
      //负面反馈条形
      drawLinePeople(listSeven){
        let date = ['分量偏少','起送价过高','食材不新鲜','送错菜','漏送菜','菜没熟','太慢']
        let moneyTotal = []
        for(let i = 0;i<listSeven.length;i++){
          moneyTotal.push(listSeven[i].orderPriceSum)
        }
        this.$stamp(null,date)
        this.$stamp(null,moneyTotal)

        echarts.init(document.getElementById('myLinePeople')).setOption({
          baseOption: {
              color: ['#FF4081'],
              tooltip: {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
              },
              xAxis: {
                  type: 'category',
                  data: date,
                  axisTick: {
                      alignWithLabel: true
                  }
              },
              yAxis: {
                  name:'人数',
                  type: 'value'
              },
              series: [{
                data: moneyTotal,
                barWidth: 40,
                color:'#FF4081',
                type: 'bar',
                label: {
                    normal: {
                        show: true,
                        position: 'top'
                    }
                }
              }]
          }
        })
      }
    },
    computed: {
      
    },
    components: {
      notice,
      Loading
    },
    created(){
      //测试网络错误情况
      // this.$api.index.index('', {        
      //     storeId: ''      
      // }).then(res=> {
      //     // 执行某些操作  
      //     console.log(res)    
      // }).catch((error) => {
      //   this.$message.error(error)
      // })
    }
}
</script>

<style>
  .index_storeMessage{
    display:flex;
    justify-content:flex-start;
